.header__menu {
  align-items: center;
  cursor: pointer;
  display: flex;
  font-size: 0;
  flex-shrink: 0;
  justify-content: center;
  position: relative;
  z-index: 15;
  width: auto;
  min-width: 30px;
  height: 40px;
}

.header__menu span {
  display: block;
  height: 3px;
  transition-duration: .25s;
  transition-property: background-color, transform;
  background: #FFF;
  width: 30px;
}

.header__menu span:after, .header__menu span:before {
  display: block;
  height: 3px;
  transition-duration: .25s;
  transition-property: background-color, transform;
}

.header__menu span:after {
  background-color: #FFF;
  content: "";
  position: absolute;
  width: inherit;
}

.header__menu span:before {
  background-color: #FFF;
  content: "";
  position: absolute;
  width: inherit;
  -ms-transform: translateY(-10px);
  transform: translateY(-10px);
}

.header__menu span:after {
  -ms-transform: translateY(10px);
  transform: translateY(10px);
}

.header__menu.is-active span {
  animation: burgerCloseA .3s;
  animation-fill-mode: forwards;
}

.header__menu.is-active span:after, .header__menu.is-active span:before {
  animation: burgerCloseB .3s;
  animation-fill-mode: forwards;
}

.header__menu.is-closing span {
  animation: burgerOpenA .3s;
  animation-fill-mode: forwards;
}

.header__menu.is-closing span:after, .header__menu.is-closing span:before {
  animation: burgerOpenB .3s;
  animation-fill-mode: forwards;
}

@keyframes burgerCloseA {
  50% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(0) rotate(-45deg);
  }
}

@keyframes burgerCloseB {
  50% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(0) rotate(90deg);
  }
}

@keyframes burgerOpenA {
  0% {
    transform: translateY(0) rotate(-45deg);
  }
  50% {
    transform: translateY(0);
  }
}

@keyframes burgerOpenB {
  0% {
    transform: translateY(0) rotate(90deg);
  }
  50% {
    transform: translateY(0);
  }
}

